'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

var setReadOnly = require( '@stdlib/utils-define-read-only-property' );


// MAIN //

/**
* Top-level namespace.
*
* @namespace tools
*/
var tools = {};

/**
* @name formatProdErrorMessage
* @memberof tools
* @readonly
* @type {Function}
* @see {@link module:@stdlib/error/tools/fmtprodmsg}
*/
setReadOnly( tools, 'formatProdErrorMessage', require( '@stdlib/error-tools-fmtprodmsg' ) );

/**
* @name id2pkg
* @memberof tools
* @readonly
* @type {Function}
* @see {@link module:@stdlib/error/tools/id2pkg}
*/
setReadOnly( tools, 'id2pkg', require( '@stdlib/error-tools-id2pkg' ) );

/**
* @name pkg2id
* @memberof tools
* @readonly
* @type {Function}
* @see {@link module:@stdlib/error/tools/pkg2id}
*/
setReadOnly( tools, 'pkg2id', require( '@stdlib/error-tools-pkg2id' ) );


// EXPORTS //

module.exports = tools;
